export default {
	data() {
		return {};
	},
	methods:{
		saveImg(){
			uni.showLoading({
				title:'正在下载中...'
			})
			uni.downloadFile({
			    url: 'https://funnyduck.raysler.com/uploadFile/rentbird/banner/images/introduce.jpg', //仅为示例，并非真实的资源
			    success: (resData) => {
			        if (resData.statusCode === 200) {
						uni.saveImageToPhotosAlbum({
							filePath: resData.tempFilePath,
							success: function() {
								uni.showToast({
									title: '分享图已保存，快去分享吧！',
									icon: 'none',
									duration: 2000
								})
							},
							fail(err) {
							
							},
							complete(data) {
								console.log(data)
								uni.hideLoading()
								if (data.errMsg === "saveImageToPhotosAlbum:fail authorize no response") {
									uni.showModal({
										title: '获取权限',
										content: '获取您拒绝过的相册权限',
										success: function(res) {
											if (res.confirm) {
												uni.openSetting({
													success(settingdata) {
														console.log(settingdata)
														if (settingdata.authSetting["scope.writePhotosAlbum"]) {
															console.log("获取权限成功，再次点击图片保存到相册")
														} else {
															console.log("获取权限失败")
														}
													},
													fail(errMsg) {
														console.log(errMsg)
													}
												})
											} else if (res.cancel) {
												console.log('用户点击取消');
											}
										}
									});
														
								}
							}
						});
			        }
			    }
			});
			
		},
		jumpMini(){
			uni.navigateToMiniProgram({
			  appId: 'wx2d48bdae69b0cd75',
			  envVersion:'release',
			  success(res) {
			    // 打开成功
			  },
			  complete(data){
				  console.log(data)
			  }
			})
		}
	}
};
